  methods: {
    handleInner() {
      this.show = !this.show;
      const left_arrow = document.querySelector(".left_arrow");
      const section_left_inner = document.querySelector(".section-left-inner");
      const section_right_outter = document.querySelector(
        ".section-right-outter"
      );
      if (!this.show) {
        left_arrow.style.right = "94%";
        left_arrow.style.top = "5px";
        section_left_inner.style.background = "none";
        section_right_outter.style.background = "none";
      } else {
        this.initHotChart();
        left_arrow.style.right = "-8%";
        left_arrow.style.top = "12px";
        section_left_inner.style.background = "rgba(22, 40, 84, 0.8)";
        section_right_outter.style.background = "rgba(22, 40, 84, 0.8)";
      }
    },
    format(percentage) {
      return `${percentage * 10},355,721`;
    },
    initHotChart() {
      this.hotChart = echarts.init(document.getElementById("hot-words-echart"));
      const option = {
        series: [
          {
            type: "wordCloud",
            sizeRange: [12, 40],
            rotationRange: [-90, 90],
            rotationStep: 45,
            gridSize: 15,
            shape: "pentagon",
            width: "100%",
            height: "100%",
            drawOutOfBound: true,
            textStyle: {
              color: function () {
                return (
                  "rgb(" +
                  [
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160),
                  ].join(",") +
                  ")"
                );
              },
            },
            emphasis: {
              textStyle: {
                shadowBlur: 10,
                shadowColor: "#333",
                color: "red",
              },
            },
            data: this.keyWords,
          },
        ],
      };
      this.hotChart.setOption(option);
    },
    handleHotTrend(item, index) {
      this.trendType.forEach((subItem) => {
        subItem.active = false;
      });
      item.active = true;
      this.hotTrendActive = index;
      this.pieData = [
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
        932,
        125,
        1934,
        4290,
        3330,
        5320,
        Math.random() * 1000,
        Math.random() * 1000,
        Math.random() * 1000,
      ];
    },
    handleHotType(item, index) {
      this.hotType.forEach((subItem) => {
        subItem.active = false;
      });
      item.active = true;
      this.hotActive = index;
    },
    handleChange(item, id) {
      const [select] = this.workList.filter((item, index) => {
        if (item.id === id) {
          this.workList[index].expand = !this.workList[index].expand;
          return item;
        }
      });
      this.workList.forEach((element) => {
        if (element.id !== select.id) {
          element.expand = true;
        }
      });
    },
  },
};
